.RuleRow {
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.RuleRow-active:hover {
  cursor: pointer;
  background: rgba(227, 242, 253, 0.5);
}

.RuleRow-disabled {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(209, 196, 233, 1)),
    color-stop(0%, rgba(209, 196, 233, 1)),
    color-stop(30%, rgba(209, 196, 233, 1)),
    color-stop(40%, rgba(227, 242, 253, 1))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 30%,
    rgba(227, 242, 253, 1) 40%
  );
  background: -o-linear-gradient(
    left,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 30%,
    rgba(227, 242, 253, 1) 40%
  );
  background: linear-gradient(
    to right,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 30%,
    rgba(227, 242, 253, 1) 40%
  );
  background-size: 300% 300%;
  -webkit-animation: Slide 1s ease 1;
          animation: Slide 1s ease 1;
}

@-webkit-keyframes Slide {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}

@keyframes Slide {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}
.RuleRow-disabled td:first-child {
  text-decoration: line-through;
}

.RuleRow-disabled:hover {
  cursor: not-allowed;
}

.RuleRow td {
  padding: 0.3em;
  border-bottom: solid 1px black;
}

.RuleRow-name {
  width: 50%;
  text-align: left;
}

.RuleRow-score {
  width: 50%;
  text-align: right;
}



